customElements.define(
  "todo-item",
  class extends HTMLElement {
    shadow: ShadowRoot;
    constructor() {
      super();
      this.shadow = this.attachShadow({ mode: "open" });
    }
    connectedCallback() {
      this.render();
    }
    render() {
      const title = this.getAttribute("title") || "";
      const checked = this.hasAttribute("checked");
      const id = this.getAttribute("id");

      this.shadow.innerHTML = `
      <style>
        .root {
          background-color: #FFF599;
          width: 100%;
          max-width: 100%;
          height: 112px;
          padding: 22px 15px;
          border-radius: 4px;
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
          box-sizing: border-box;
          font-family: 'Roboto', sans-serif;
        }   
        .title {
          font-family: 'Roboto', sans-serif;
          font-size: 18px;
          margin: 0;
          max-width: 80%;
          word-break: break-word;
        }
        .title.checked {
          text-decoration: line-through;
          color: #555;
        }
        .right-side {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 15px;
        }
        .checkbox {
          width: 20px;
          height: 20px;
          cursor: pointer;
        }
        .delete-btn {
          cursor: pointer;
          border: none;
          background: none;
          padding: 0;
          display: ${checked ? "block" : "none"}; /* Solo se ve si est√° completada */
        }
      </style>
      <div class="root">
        <h3 class="title ${checked ? "checked" : ""}">${title}</h3>
        <div class="right-side">
          <input class="checkbox" type="checkbox" ${checked ? "checked" : ""} />
          <button class="delete-btn">üóëÔ∏è</button>
        </div>
      </div>
    `;

      this.addListeners();
    }

    addListeners() {
      const id = this.getAttribute("id");

      this.shadow.querySelector(".checkbox").addEventListener("change", (e: any) => {
        this.dispatchEvent(new CustomEvent("change", {
          detail: { id, value: e.target.checked }
        }));
      });

      this.shadow.querySelector(".delete-btn").addEventListener("click", () => {
        this.dispatchEvent(new CustomEvent("delete", {
          detail: { id }
        }));
      });
    }
  }
);