class TextComponent extends HTMLElement {
  shadow: ShadowRoot;
  tags: string[] = ["h1", "p", "label"];
  tag: string = "p";

  constructor() {
    super();
    this.shadow = this.attachShadow({ mode: "open" });
  }

  connectedCallback() {
    // Definimos el tag bas√°ndonos en el atributo o usamos 'p' por defecto
    const attrTag = this.getAttribute("tag");
    if (this.tags.includes(attrTag)) {
      this.tag = attrTag;
    }
    this.render();
  }

  render() {
    const rootEl = document.createElement(this.tag);
    rootEl.textContent = this.textContent;

    // Un poco de estilo b√°sico para que se vea como Figma
    rootEl.style.margin = "0";
    rootEl.style.fontFamily = "sans-serif";

    this.shadow.innerHTML = ""; // Limpiamos antes de agregar
    this.shadow.appendChild(rootEl);
  }
}

customElements.define("my-text", TextComponent);