import { state } from "../../state";

export function initHomePage(containerEl: HTMLElement) {
  function render() {
    containerEl.innerHTML = `
      <style>
        .main-wrapper {
          display: flex;
          flex-direction: column;
          min-height: 100vh;
          font-family: 'Roboto', sans-serif;
        }

        .header {
          background-color: #FF8282;
          height: 60px;
          width: 100%;
        }

        .main-content {
          max-width: 1020px;
          margin: 0 auto;
          padding: 40px 20px;
          width: 100%;
          box-sizing: border-box;
        }

        .header-title {
          font-size: 52px;
          line-height: 0.9;
          margin: 0 0 30px 0;
          font-weight: 800;
          /* Fuerza el salto de l√≠nea en mobile como en Figma */
          max-width: 250px;
        }

        .form {
          display: flex;
          flex-direction: column;
          gap: 5px;
          margin-bottom: 50px;
        }

        .form label {
          font-size: 18px;
          margin-bottom: 5px;
          font-family: 'Roboto', sans-serif;
        }

        .input-group {
          display: flex;
          flex-direction: column;
          gap: 12px;
        }

        .form input {
          height: 55px;
          border: 2px solid #000000;
          border-radius: 4px;
          padding: 10px;
          font-size: 18px;
          width: 100%;
        }

        .form button {
          height: 55px;
          background-color: #9CB5E1;
          border: none;
          border-radius: 4px;
          font-size: 20px;
          font-weight: 500;
          cursor: pointer;
        }

        /* CONTENEDOR DE CARDS: Comportamiento Mobile */
        .grid-tasks {
          display: flex;
          flex-direction: column;
          gap: 20px;
          width: 100%;
          /* Esto obliga a que las cards amarillas se estiren
             al mismo ancho que el input/bot√≥n */
          align-items: stretch;
        }

        /* MEDIA QUERY PARA DESKTOP */
        @media (min-width: 600px) {
          .header-title {
            max-width: none;
            line-height: 1.2;
          }

          .input-group {
            flex-direction: row;
            align-items: flex-end;
            gap: 10px;
          }

          .form input {
            flex-grow: 1;
          }

          .form button {
            width: 312px;
          }

          .grid-tasks {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
            justify-content: center;
            align-items: start;
          }
        }
      </style>

      <div class="main-wrapper">
        <header class="header"></header>

        <main class="main-content">
          <h1 class="header-title">Mis pendientes</h1>

          <form class="form">
            <label>Nuevo pendiente</label>
            <div class="input-group">
              <input name="task" type="text" placeholder="Escribe aqu√≠ tu tarea...">
              <button type="submit">Agregar</button>
            </div>
          </form>

          <div class="grid-tasks">
            </div>
        </main>
      </div>
    `;

    const tasksContainer = containerEl.querySelector(".grid-tasks");
    const tasks = state.getEnabledTasks();

    tasks.forEach((t: any) => {
      const item = document.createElement("todo-item");
      item.setAttribute("title", t.title);
      item.setAttribute("id", t.id.toString());
      if (t.completed) item.setAttribute("checked", "true");

      item.addEventListener("change", (e: any) => {
        state.changeItemState(e.detail.id, e.detail.value);
      });

      item.addEventListener("delete", (e: any) => {
        state.deleteTask(e.detail.id);
      });

      tasksContainer.appendChild(item);
    });

    const form = containerEl.querySelector(".form");
    form.addEventListener("submit", (e: any) => {
      e.preventDefault();
      const input = e.target.task;
      if (input.value.trim() !== "") {
        state.addTask(input.value);
        input.value = ""; 
      }
    });
  }

  state.subscribe(render);
  render();
}